.videos-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  .video-item {
      position: relative;
      height: 2rem;
      margin: 0.1rem 0.1rem;
      video {
          height: 100%;
          z-index: 1;
      }
      .play-icon {
          position: absolute;
          display: flex;
          justify-content: center;
          align-items: center;
          left: 50%;
          top: 50%;
          width: 0.4rem;
          height: 0.4rem;
          margin-left: -0.2rem;
          margin-top: -0.2rem;
          background: rgb(208, 204, 204);
          opacity: 0.8;
          border-radius: 50%;
          z-index: 2;
          cursor: pointer;
          i {
              width: 0;
              height: 0;
              margin-left: 0.1rem;
              border: 0.1rem solid;
              border-color: transparent transparent transparent #fff;
          }
      }
  }
  .play-box {
      position: absolute;
      left: 50%;
      top: 50%;
      width: 60%;
      transform: translate(-50%, -50%);
      border: 1px solid #000;
      z-index: 2;
      video {
          width: 100%;
          height: 100%;
          max-height: 600px;
          z-index: 1;
      }
      .close-icon {
          position: absolute;
          right: 0.05rem;
          top: 0.05rem;
          width: 0.1rem;
          height: 0.1rem;
          background-image: url('../../../image/close.png');
          background-size: 0.1rem 0.1rem;
          cursor: pointer;
          z-index: 2;
      }
  }
}